import React from 'react';
import '../../App.css';
import video1 from '../../beats/1.mp4'
import video2 from '../../beats/2.mp4'
import video3 from '../../beats/3.mp4'

function FadeInSection(props) {
  const [isVisible, setVisible] = React.useState(true);
  const domRef = React.useRef();
  React.useEffect(() => {
    const observer = new IntersectionObserver(entries => {
      entries.forEach(entry => setVisible(entry.isIntersecting));
    });
    observer.observe(domRef.current);
  }, []);
  return (
    <div
      className={`fade-in-section ${isVisible ? 'is-visible' : ''}`}
      ref={domRef}
    >
      {props.children}
    </div>
  );
}

export default function Content() {
  return (
    <>
      <h className='content'>CONTENT</h> 
      <div className='content-items'>
        <FadeInSection>
        <div className='content-item'>
          <div className='video-border'>
            <video className='video' src={video1} controls/>
          </div>
          <body className='metadata'>
            Name: <br></br>
            Length: <br></br>
            Frames: <br></br>
            BPM: <br></br>
            Visual Art: <br></br>
            Date of Recording: <br></br>
          </body>
        </div>
        </FadeInSection>
        <FadeInSection>
        <div className='content-item'>
          <div className='video-border'>
            <video className='video' src={video2} controls/>
          </div>
          <body className='metadata'>
            Name: <br></br>
            Length: <br></br>
            Frames: <br></br>
            BPM: <br></br>
            Visual Art: <br></br>
            Date of Recording: <br></br>
          </body>
        </div>
        </FadeInSection>
        <FadeInSection>
        <div className='content-item'>
          <div className='video-border'>
            <video className='video' src={video3} controls/>
          </div>
          <body className='metadata'>
            Name: <br></br>
            Length: <br></br>
            Frames: <br></br>
            BPM: <br></br>
            Visual Art: <br></br>
            Date of Recording: <br></br>
          </body>
        </div>
        </FadeInSection>
      </div>
    </>
  );
}